import * as THREE from 'three';
import * as CANNON from 'cannon-es';

import Stats from 'three/examples/jsm/libs/stats.module.js';
import {SpeechBubble} from './SpeechBubble';
import { MainCharacter } from './Character';
import { OtherCharacter } from './OtherCharacter';
import { ThirdPersonCamera } from './ThirdPersonCamera';
import {CombineControllerInput} from './CombineControllerInput';
import {Environment} from './Environment';
import {io} from "socket.io-client";
import { v4 as uuid } from 'uuid';
import { Status } from './Status';
import { optionsForStatus } from './Data';
import {GameCircle} from './GameCircle';
import CannonDebugRenderer from '../utils/cannonDebugRenderer';

const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;
const POSSIBLE_STATUS = ['chess','ludo','car']

interface BotObject{
  mesh: OtherCharacter
}

interface PlayerPulseData{
  publicAddress: string,
  characterId: string,
  position: {x:number, y:number, z:number},
  rotation: {x:number, y:number, z:number},
  state: string,
  status: string,
  statusVisible: boolean
}

interface ChoiceSelectionData{
  choice: string,
  usersInvolved: string[]
}

interface StatusUpdatedData{
  status: string,
  public_address: string,
}

interface UpdateStatusData{
  status: string,
  public_address: string,
}

interface StatusActionData{
  action: string,
  sender: string,
  receiver: string,
  status: string
}

function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

export class KLSpace {

  private _renderer:THREE.WebGLRenderer;
  private _scene:THREE.Scene;
  private _thirdPersonCamera:ThirdPersonCamera;
  private mainCharacter:MainCharacter;
  private otherCharacter1:OtherCharacter;
  private _previousRAF:number = null;
  private _domRoot:any;
  private speechBubble:SpeechBubble;
  private environment:Environment;
  private combineControllerInput:CombineControllerInput;
  private otherCharacterMap:Map<string, OtherCharacter> = new Map();
  private charactersHeartBeat:Map<string, Number> = new Map();
  private socket:any;
  private messageDialogs:Map<string, Status> = new Map();
  private intentFor:string = undefined;

  private _statusActionDiv: any
  private _statusActionYesButton: any
  private _statusActionNoButton: any
  private _statusActionCancelButton: any
  private _statusActionText: any

  private timer: any;
  private countDown: number;
  private gameCircle:GameCircle;

  private _world: CANNON.World;
  private stats:Stats;
  private cannonDebugRenderer: CannonDebugRenderer;

  move(playerKeys){
    this.mainCharacter.action.forward = playerKeys.forward;
    this.mainCharacter.action.backward = playerKeys.backward;
    this.mainCharacter.action.left = playerKeys.left;
    this.mainCharacter.action.right = playerKeys.right;
    this.mainCharacter.action.space = playerKeys.space;
    this.mainCharacter.action.shift = playerKeys.shift;
  }

  constructor(domRoot:any, statusActionDiv:any) {
    this._domRoot=domRoot;
    this._statusActionDiv = statusActionDiv;

    // this._statusActionDiv.style.visibility = "hidden";
    console.log("DIV", this._statusActionDiv);
    this._statusActionYesButton = this._statusActionDiv.getElementsByClassName('status_action_dialog_button_yes')[0]
    this._statusActionNoButton = this._statusActionDiv.getElementsByClassName('status_action_dialog_button_no')[0]
    this._statusActionCancelButton = this._statusActionDiv.getElementsByClassName('status_action_dialog_button_cancel')[0]
    this._statusActionText = this._statusActionDiv.getElementsByClassName('status_action_dialog_content')[0]

    this._statusActionYesButton.onclick = () => {
      this.statusDialogOnClickYes()
    }

    this._statusActionNoButton.onclick = () => {
      this.statusDialogOnClickNo()
    }

    this._statusActionCancelButton.onclick = () => {
      this.statusDialogOnClickCancel()
    }

    this.socket = io("https://young-stream-43548.herokuapp.com").connect()

    this._scene = new THREE.Scene();
    this._world = new CANNON.World();
    this.stats = Stats();
    document.body.appendChild(this.stats.dom);
    
    this.cannonDebugRenderer = new CannonDebugRenderer(this._scene, this._world);
    this._Initialize();
  }

  showStatusActionDialog(){
    this._statusActionDiv.style.visibility = "visible";
    this._statusActionYesButton.style.visibility = "visible";
    this._statusActionNoButton.style.visibility = "visible"
    this._statusActionCancelButton.style.visibility = "hidden"
    this._statusActionText.innerHTML = `Play chess with ${this.intentFor}?`
  }

  hideStatusActionDialog(){
    this._statusActionDiv.style.visibility = "hidden";
    this._statusActionYesButton.style.visibility = "hidden";
    this._statusActionNoButton.style.visibility = "hidden"
    this._statusActionCancelButton.style.visibility = "hidden"
  }

  statusDialogOnClickYes(){
    console.log("Yes clicked")
    console.log("Intent for", this.intentFor)
    console.log("Socket", this.socket)
    if(this.socket && this.intentFor){
      // if(this.socket){
      this.socket.emit('statusAction', JSON.stringify({
        action: "yes",
        sender: this.mainCharacter.PublicAddress,
        receiver: this.intentFor,
        status: "chess"
      }))

      this.showTeleportingDialog()
    }
  }

  statusDialogOnClickNo(){
    console.log("No clicked")
    this.hideStatusActionDialog()
  }

  statusDialogOnClickCancel(){
    console.log("Cancel clicked")
    if(this.socket && this.intentFor){
      // if(this.socket){
      this.socket.emit('statusAction', JSON.stringify({
        action: "cancel",
        sender: this.mainCharacter.PublicAddress,
        receiver: this.intentFor,
        status: "chess"
      }))
      this.dismisTeleport()
      this.showStatusActionDialog()
    }
  }

  showTeleportingDialog(){
    this._statusActionDiv.style.visibility = "visible";
    this._statusActionYesButton.style.visibility = "hidden";
    this._statusActionNoButton.style.visibility = "hidden"
    this._statusActionCancelButton.style.visibility = "visible"
    this._statusActionText.innerHTML = "Teleporting you to the chess room..."
    //Initiate countdown here
    if(!this.timer){
      this.countDown = 5;
      this.timer = setInterval(() => {
        this.countDown--;
        if(this.countDown == 0){
          console.log("Teleporting...")
          this.dismisTeleport()
        }
      }, 1000);
    }

  }

  dismisTeleport(){
    this.hideStatusActionDialog()
    clearInterval(this.timer)
    this.timer = null
  }

  addEnvironment(){
    this.environment = new Environment();
    this.environment.load(this._scene);
  }

  _Initialize() {
    this._renderer = new THREE.WebGLRenderer({
      antialias:true, 
      alpha: true
    });
    this._renderer.outputEncoding = THREE.sRGBEncoding;
    this._renderer.shadowMap.enabled = true;
    this._renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    //this._renderer.setPixelRatio(window.devicePixelRatio);
    this._renderer.setSize(window.innerWidth, window.innerHeight);

    this._domRoot.innerHTML = '';
    this._domRoot.append(this._renderer.domElement);

    this.gameCircle = new GameCircle(this._scene);

    this.addLights();
    this.addSky();
    //this.addGround();
    this.addDialoag();
    this.addCharacter();
    this.addEnvironment();
    //this.addRaycast();
    this.initSockets();
    

    (window as any).onSelectIntent = (choiceIdentifier: string) => {
      console.log("Choice selected", choiceIdentifier)
      if(this.socket){
        this.socket.emit('onChoiceSelected', JSON.stringify({
          choice: choiceIdentifier,
          usersInvolved: [this.mainCharacter.PublicAddress, this.intentFor],
        }))
      }
    }

    this._RAF();

    (window as any).onresize = this._OnWindowResize;




  }



  addDialoag(){
    this.speechBubble = new SpeechBubble(this._scene, 33, "Hey, Welcome to Kholand.", 10);
		this.speechBubble.mesh.position.set(3, 23, 0);
  }

  addGround(){
    const plane = new THREE.Mesh(
        new THREE.PlaneGeometry(100, 100, 10, 10),
        new THREE.MeshStandardMaterial({
            color: 0x90ee90,
          }));
    plane.castShadow = false;
    plane.receiveShadow = true;
    plane.rotation.x = -Math.PI / 2;
    this._scene.add(plane);
  }

  addSky(){
    const loader = new THREE.CubeTextureLoader();
    const texture = loader.load([
      `${PUBLIC_URL}/images/skybox-2/right.png`,
      `${PUBLIC_URL}/images/skybox-2/left.png`,
      `${PUBLIC_URL}/images/skybox-2/top.png`,
      `${PUBLIC_URL}/images/skybox-2/bottom.png`,
      `${PUBLIC_URL}/images/skybox-2/front.png`,
      `${PUBLIC_URL}/images/skybox-2/back.png`,
    ]);
    texture.encoding = THREE.sRGBEncoding;
    this._scene.background = texture;
  }

  light = new THREE.DirectionalLight(0xFFFFFF, 1.0);

  addLights(){
    const light = this.light;
    light.position.set(-100, 100, 100);
    light.target.position.set(0, 0, 0);
    light.castShadow = true;
    light.shadow.bias = -0.001;
    light.shadow.mapSize.width = 4096;
    light.shadow.mapSize.height = 4096;
    light.shadow.camera.near = 0.1;
    light.shadow.camera.far = 10000.0;
    light.shadow.camera.near = 0.5;
    light.shadow.camera.far = 500.0;
    const side = 100;
    light.shadow.camera.left = side;
    light.shadow.camera.right = -side;
    light.shadow.camera.top = side;
    light.shadow.camera.bottom = -side;
    this._scene.add(light);

    const ambientLight = new THREE.AmbientLight(0xFFFFFF, 0.25);
    this._scene.add(ambientLight);

    // const shadowHelper = new THREE.CameraHelper( light.shadow.camera );
    // this._scene.add( shadowHelper );
  }

  addCharacter() {
    const scope = this;
    const gender = ['m', 'f'];
    const characterId = `${gender[getRandomInt(2)]}_${getRandomInt(11)+1}`
    const publicAddress= uuid();
    const initialPosition = new THREE.Vector3(getRandomInt(40), 0 , getRandomInt(40));
    this.mainCharacter = new MainCharacter(
      this._scene, 
      characterId, 
      initialPosition,
      publicAddress,
      this.onMainCharacterLoaded);
    this.combineControllerInput = new CombineControllerInput(this.mainCharacter);
    this._thirdPersonCamera = new ThirdPersonCamera(this.mainCharacter, window.innerWidth, window.innerHeight);
  }

  onMainCharacterLoaded = () =>{
      // Set signal now.
      console.log("mainCharacter loaded");
      const scope = this;
      this.socket.emit('register', JSON.stringify({
        publicAddress:scope.mainCharacter.PublicAddress,
        characterId:scope.mainCharacter.CharacterId,
        position:scope.mainCharacter.Position,
        status: scope.mainCharacter.Status,
        statusVisible: scope.mainCharacter.StatusVisible
      }));
  }

  initSockets = () => {

    const scope = this
    const socket = scope.socket;

    socket.on('connect', ()=>{
      console.log('connected');
    });

    socket.on('registered', (resp)=>{
      const data = JSON.parse(resp);
      console.log('registered.data', data);
      const _id = data.id;
      const _publicAddress = data.publicAddress;
      const _postion = data.position;

      console.log('My PublicAddress is: ', _publicAddress, "position", _postion);
      const newPosition = new THREE.Vector3(_postion.x, _postion.y, _postion.z);
      scope.mainCharacter.UpdatePosition(newPosition)
      scope.combineControllerInput.enableInput();
    });

    socket.on('newUserRegistered', (resp)=>{
      const data = JSON.parse(resp);
      console.log('newUserRegistered.data', data);
    });

    //Update when one of the users moves in space
    socket.on('remoteData',  (resp:string ) =>{
      const playerDataList = JSON.parse(resp);
      const scope = this;
      playerDataList.forEach((playerData:PlayerPulseData) => {
        if( scope.mainCharacter && scope.mainCharacter.PublicAddress == playerData.publicAddress){
          return;
        }

        // otherplayes
        const publicAddress = playerData.publicAddress;
        const characterId = playerData.characterId;
        const positionVector = new THREE.Vector3(
          playerData.position.x,
          playerData.position.y,
          playerData.position.z);
        const status = playerData.status
        const statusVisible = playerData.statusVisible
        
        const newOtherPlayer = scope.otherCharacterMap.has(publicAddress)?
          scope.otherCharacterMap.get(publicAddress):
          new OtherCharacter(scope._scene, characterId, positionVector, status, statusVisible, ()=>{  });

        newOtherPlayer.ChangeState(playerData.state);
        newOtherPlayer.UpdatePosition(positionVector);
        newOtherPlayer.ChangeStatus(status)
        newOtherPlayer.ChangeStatusVisibility(statusVisible)
        scope.otherCharacterMap.set(publicAddress, newOtherPlayer);
        scope.charactersHeartBeat[publicAddress] = new Date().getTime();

      });

    
      const hearBeats = Object.keys(scope.charactersHeartBeat);
      hearBeats.forEach((publicAddress:string)=>{
        const now = new Date().getTime();
        const lastHeartBeat = scope.charactersHeartBeat[publicAddress];
        if(now - lastHeartBeat > 10000){
          const otherPlayer = scope.otherCharacterMap.get(publicAddress);
          if(otherPlayer){
            otherPlayer.Kill()
            scope.otherCharacterMap.delete(publicAddress);
            scope.charactersHeartBeat.delete(publicAddress);
          }
        
        }
      })

    });

    socket.on('initiateGamePlay', (jsonString)=>{
      const data: ChoiceSelectionData = JSON.parse(jsonString);
      console.log('Got Gameplay data', data);
      if(data.usersInvolved.find((ele)=> ele == scope.mainCharacter.PublicAddress)){
        console.log("Travelling to game")
        //@ts-ignore
        Android.travelToGame()
      }
    });

    // socket.on('statusUpdated', (jsonData: string) => {
    //   const data: StatusUpdatedData = JSON.parse(jsonData);
    //   const playerPublicAddress = data.public_address
    //   if(scope.otherCharacterMap.has(playerPublicAddress)){
    //     const otherPlayer = scope.otherCharacterMap.get(playerPublicAddress);
    //     otherPlayer.ChangeStatus(data.status);
    //   }
    // })

    socket.on('statusActionUpdated', (jsonData: string) => {
      console.log("statusActionUpdated", jsonData);
      const data: StatusActionData = JSON.parse(jsonData);
      const sender = data.sender
      const receiver = data.receiver
      const action = data.action
      
      //The signal is for me, so act on it else ignore it.
      if(receiver == scope.mainCharacter.PublicAddress){
        console.log("statusAction for me",data)
        if(action == 'yes'){
          //Start the taking to game countdown
          scope.showTeleportingDialog()
        }
        else if(action == 'cancel'){
          //Stop the taking to game countdown if it is running
          scope.dismisTeleport()
        }
      }

    })

  
    //   // Set player TTL and kil after 1 min.

    setInterval(this.sendLocation.bind(this), 100)

  }


  sendLocation() {
    const scope = this;
    const publicAddress = scope.mainCharacter.PublicAddress;
    const position = scope.mainCharacter.Position;
    const rotation = scope.mainCharacter.Rotation;
    scope.socket.emit('move', JSON.stringify({
      publicAddress: publicAddress,
      position: {x:position.x,y:position.y,z:position.z},
      rotation: {x:rotation.x,y:rotation.y,z:rotation.z},
      state: scope.mainCharacter.StateName
    }));
  }


  _OnWindowResize() {
    this._thirdPersonCamera && this._thirdPersonCamera.resize(window.innerWidth, window.innerHeight);
    this._renderer && this._renderer.setSize(window.innerWidth, window.innerHeight);
  }

  _RAF() {
    requestAnimationFrame((t) => {
      if (this._previousRAF === null) {
        this._previousRAF = t;
      }

      this._RAF();
      this._renderer.render(this._scene, this._thirdPersonCamera.getThreeCamera());
      this._Step(t - this._previousRAF);
      this._previousRAF = t;
    });
  }


  _Step(timeElapsed) {
    const timeElapsedS = timeElapsed * 0.001;
    if (this.mainCharacter) {
      this.mainCharacter.Update(timeElapsedS);
    }



    this.stats.update();
    if(this.cannonDebugRenderer){
      this.cannonDebugRenderer.update();
    }

    const p = this._thirdPersonCamera.getThreeCamera().position;
    this.light.lookAt(p.x, p.y, p.z);

    if(this.otherCharacter1){
      this.otherCharacter1.Update(timeElapsedS);
    }

    if(this.gameCircle){
      this.gameCircle.Update();
    }

    // if (this.speechBubble) {
        // const postion = this.otherCharacter1.Position;
        // const lookAt = this._thirdPersonCamera.getThreeCamera().position;
        // this.speechBubble.Update(postion, lookAt);
    // }

    const scope = this;
    this.otherCharacterMap.forEach((otherCharacter)=>{
      otherCharacter.Update(timeElapsedS);
    });

    this._thirdPersonCamera.Update(timeElapsedS);




    //Other Sol
    if(scope.mainCharacter.Object){

    const myBox = new THREE.Box3().setFromObject(scope.mainCharacter.Object)
    myBox.expandByScalar(10.0)
    
    const otherPlayersKeys = Array.from(scope.otherCharacterMap.keys())
    for(let key of otherPlayersKeys){
      const otherPlayer = scope.otherCharacterMap.get(key);
      if(!otherPlayer.Object)continue
      const otherBox = new THREE.Box3().setFromObject(otherPlayer.Object)
      if(myBox.intersectsBox(otherBox)){
        if(!scope.intentFor){
          // console.log("Intent not visible, show for this address", key);
          scope.intentFor = key;
          scope.showStatusActionDialog()
        }
        else if(scope.intentFor === key){
          // console.log("Intent already visible for this address, do nothing", key);
        }
        else if(scope.intentFor !== key){
          // console.log("Intent already visible for another address, wait for it to go", key);
        }

        if(!scope.messageDialogs.has(key)){
            //Show a dialog if the other player is close to me.
            const bubblePosition = otherPlayer.Position.clone();
            bubblePosition.y = 28
            const status = new Status(
              scope._scene,
              bubblePosition,
               otherPlayer.publicAddress,
               otherPlayer.Status,
               (obj)=>{}
            );
            scope.messageDialogs.set(key, status);
            
            
            // status.Object.position.set(bubblePosition.x, bubblePosition.y, bubblePosition.z);
        }
      }
      else{
        if(!scope.intentFor){
          // console.log("Intent not visible, do nothing", key);
        }
        else if(scope.intentFor === key){
          // console.log("Intent visible for non intersecting this address, hide", key);
          scope.intentFor = undefined;
          scope.hideStatusActionDialog()
        }
        else if(scope.intentFor !== key){
          // console.log("Intent not visible for non intersecting another address, do nothing", key);
        }
        if(scope.messageDialogs.has(key)){
          //Remove the dialog if the other player is far away.
          const bubble = scope.messageDialogs.get(key)
          scope._scene.remove(bubble.Object)
          scope.messageDialogs.delete(key)
        }
      }
    }

  }

    // console.log("Intnet", scope.intentFor);

    //Remove the dialog if the other player does not exist anymore in the world
    const dialogs = Array.from(scope.messageDialogs.keys())
    for(let key of dialogs){
      scope.messageDialogs.get(key).Update(timeElapsedS);
      if(!scope.otherCharacterMap.has(key)){
          const bubble = scope.messageDialogs.get(key)
          scope._scene.remove(bubble.Object)
          scope.messageDialogs.delete(key)
      }
    }


    //

    

  }

  addRaycast(){
    document.body.appendChild(this._renderer.domElement);
    document.addEventListener('touchend', onDocumentTouchEnd, false);
    document.addEventListener( 'mousemove', onPointerMove );

    const scope = this;
    const camera = scope._thirdPersonCamera.getThreeCamera();

    const pointer = new THREE.Vector2();

    function performRaycast(cord:THREE.Vector2){
      const raycaster = new THREE.Raycaster(  );
      raycaster.setFromCamera(cord, camera);
      const intersects = raycaster.intersectObjects(scope._scene.children, true );
      console.log("intersects", intersects);
    }

    function onPointerMove( event ) {
      pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
      pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
      performRaycast(pointer);
    }

    function onDocumentTouchEnd(event) {
        event.preventDefault();
        pointer.x = (event.changedTouches[0].clientX / window.innerWidth) * 2 - 1;
        pointer.y = -(event.changedTouches[0].clientY / window.innerHeight) * 2 + 1;
        performRaycast(pointer);
    }
  }
}

