import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import {CharacterFSM} from './CharacterStateMachine';


const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;
class BasicCharacterControllerProxy {

  private  _animations:THREE.AnimationClip;
  constructor(animations:THREE.AnimationClip) {
    this._animations = animations;
  }

  get animations() {
    return this._animations;
  }
};

const ANIMATIONS = ['Dance', 'Idle', 'Run', 'Walk'];
const SCALE = 12.9;

export class MainCharacter {
  private scene:THREE.Scene;
  private _decceleration:THREE.Vector3;
  private _acceleration:THREE.Vector3;
  private _velocity:THREE.Vector3;
  private _stateMachine:CharacterFSM;
  public _target:THREE.Object3D;
  private _mixer:THREE.AnimationMixer;
  private _mixerPlayer:THREE.AnimationMixer;
  private _animations:any;
  private _characterId:string;
  private _position=new THREE.Vector3();
  private _onLoadModel:CallableFunction;
  private _publicAddress: string;

  private _status:string
  private _statusVisible: boolean = false;

  public action = {
    forward: false,
    backward: false,
    left: false,
    right: false,
    space: false,
    shift: false,
  };

  constructor(scene:THREE.Scene, characterId:string, position:THREE.Vector3, publicAddress:string, onLoad:CallableFunction) {
    this.scene=scene;
    this._characterId=characterId;
    this._position = position;
    this._onLoadModel = onLoad;
    this._publicAddress = publicAddress;
    this._Init();
  }

  _Init() {
    this._decceleration = new THREE.Vector3(-0.0005, -0.0001, -5.0);
    this._acceleration = new THREE.Vector3(1, 0.25, 60.0);
    this._velocity = new THREE.Vector3(0, 0, 0);

    this._animations = {};
    this._stateMachine = new CharacterFSM(
        new BasicCharacterControllerProxy(this._animations));

    this._LoadModels();
  }

  loadAnimationMale = async(gender, callback:CallableFunction)=>{
    const loader = new GLTFLoader();
    const animationDir = `${PUBLIC_URL}/models/cartoon-characters-3-15/gltf/animation`
    const idle = await loader.loadAsync(`${animationDir}/Idle-${gender}.gltf`);
    const walk = await loader.loadAsync(`${animationDir}/Walking-${gender}.gltf`);
    const run = await loader.loadAsync(`${animationDir}/Running-${gender}.gltf`);
    const dance = await loader.loadAsync(`${animationDir}/Dance.gltf`);

    Promise.all([idle, walk, run, dance]).then((glTFList) => {
      callback(glTFList.map(v=>v.animations[0]));
    });
  }

  _LoadModels() {
    const scope = this;
    const loader = new GLTFLoader();
    // TODO: Fix f_8, f_11 not working.
    if(this._characterId == 'f_8' || this._characterId == 'f_11'){
      this._characterId = 'f_1';
    }
    const url = `${PUBLIC_URL}/models/cartoon-characters-3-15/gltf/${this._characterId}.gltf`;
    loader.load(url, (gltf) => {
      gltf.scene.scale.set(SCALE,SCALE,SCALE);
      gltf.scene.traverse(object3D => {
        object3D.castShadow = true;
      });

      const gender = scope._characterId[0];
      scope.loadAnimationMale(gender,(animations:THREE.AnimationClip[])=>{
        scope._target = gltf.scene;
        scope._target.position.copy(scope._position);
        
        scope.scene.add(scope._target);
  
        scope._mixer = new THREE.AnimationMixer(scope._target);
        ANIMATIONS.forEach(animationName=>{
          const clip = THREE.AnimationClip.findByName(animations, animationName);
          const action = scope._mixer.clipAction(clip);
          scope._animations[animationName.toLowerCase()] = {
            clip: clip,
            action: action,
          };
        });

        scope._stateMachine.SetState('idle');
        scope._onLoadModel();
      })

    });
  }

  get PublicAddress(){
    return this._publicAddress;
  }

  get CharacterId(){
    return this._characterId;
  }

  get Position() {
    if(this._target===undefined){
      return this._position;
    }
    else{
      return this._target.position;
    }
  }

  get StateName(){
    if(this._stateMachine!==undefined && 
      this._stateMachine._currentState!==undefined && 
      this._stateMachine._currentState.Name!==undefined)return this._stateMachine._currentState.Name
    return 'idle'
  }

  get Rotation() {
    if (!this._target) {
      return new THREE.Quaternion();
    }
    return this._target.quaternion;
  }

  get Object(){
    return this._target;
  }

  get Status(){
    return this._status
  }

  get StatusVisible(){
    return this._statusVisible
  }

  UpdateStatus(status:string){
    this._status = status
  }

  UpdateStatusVisible(statusVisible:boolean){
    this._statusVisible = statusVisible 
  }

  UpdatePosition(newPosition:THREE.Vector3){
    this._target.position.copy(newPosition);
  }

  Update(timeInSeconds) {
    if (!this._stateMachine._currentState) {
      return;
    }

    this._stateMachine.Update(timeInSeconds, this.action);

    const velocity = this._velocity;
    const frameDecceleration = new THREE.Vector3(
        velocity.x * this._decceleration.x,
        velocity.y * this._decceleration.y,
        velocity.z * this._decceleration.z
    );
    frameDecceleration.multiplyScalar(timeInSeconds);
    frameDecceleration.z = Math.sign(frameDecceleration.z) * Math.min(
        Math.abs(frameDecceleration.z), Math.abs(velocity.z));

    velocity.add(frameDecceleration);

    const controlObject = this._target;
    const _Q = new THREE.Quaternion();
    const _A = new THREE.Vector3();
    const _R = controlObject.quaternion.clone();

    const acc = this._acceleration.clone();
    if (this.action.shift) {
      acc.multiplyScalar(2.0);
    }

    if (this._stateMachine._currentState.Name == 'dance') {
      acc.multiplyScalar(0.0);
    }

    if (this.action.forward) {
      velocity.z += acc.z * timeInSeconds;
    }
    if (this.action.backward) {
      velocity.z -= acc.z * timeInSeconds;
    }
    if (this.action.left) {
      _A.set(0, 1, 0);
      _Q.setFromAxisAngle(_A, 2.0 * Math.PI * timeInSeconds * this._acceleration.y);
      _R.multiply(_Q);
    }
    if (this.action.right) {
      _A.set(0, 1, 0);
      _Q.setFromAxisAngle(_A, 2.0 * -Math.PI * timeInSeconds * this._acceleration.y);
      _R.multiply(_Q);
    }

    controlObject.quaternion.copy(_R);

    const oldPosition = new THREE.Vector3();
    oldPosition.copy(controlObject.position);

    const forward = new THREE.Vector3(0, 0, 1);
    forward.applyQuaternion(controlObject.quaternion);
    forward.normalize();
    forward.multiplyScalar(velocity.z * timeInSeconds);
    controlObject.position.add(forward);

    this._position.copy(controlObject.position);

    if (this._mixer) {
      this._mixer.update(timeInSeconds);
    }
    if(this._mixerPlayer){
      this._mixerPlayer.update(timeInSeconds);
    }
  }
};
