import * as THREE from 'three';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import {SpeechBubble} from './SpeechBubble';
import { MainCharacter } from './Character';
import {KLSpace} from './KLSpace';
import {GameControl} from "../GameControl/GameControl";

const PUBLIC_URL = process.env.PUBLIC_URL;

import React, { useRef, useEffect } from 'react';


export const KholandSpace = props => {

  const divRef = useRef(null)
  let _APP:KLSpace;

  const handleJoystickStart = (evt, data)=>{
  };

  const handleJoystickEnd = (evt, data)=>{
    const keys = {
      forward: false,
      backward: false,
      left: false,
      right: false,
      space: false,
      shift: false,
    };
    _APP.move(keys);
  };

  const handleJoystickMove = (evt, data)=>{
    const {x,y} = data.vector;
    const keys = {
      forward: false,
      backward: false,
      left: false,
      right: false,
      space: false,
      shift: false,
    };
    if(y>0.7){
      keys.forward=true;
    }else if(y < -0.7){
      keys.backward=true;
    }
    if(x>0.7){
      keys.right=true;
    }else if(x < -0.7){
      keys.left=true;
    }

    _APP.move(keys);
  };

  const handleJoystickDir = (evt, data)=>{
  };

  const handleJoystickPressure = (evt, data)=>{
  };

  const statusActionDialogRef = useRef(null)
  const joyStickRef = useRef(null)

  useEffect(() => {  
    const div = divRef.current
    const statusActionDiv = statusActionDialogRef.current

    _APP = new KLSpace(div, statusActionDiv);

    let animationFrameId

    //Our draw came here
    const render = () => {
      animationFrameId = window.requestAnimationFrame(render)
    }
    render()

    return () => {
      window.cancelAnimationFrame(animationFrameId)
    }
  }, []);
  
  return (<>
      <GameControl
        start = {handleJoystickStart}
        end = {handleJoystickEnd}
        move = {handleJoystickMove}
        dir = {handleJoystickDir}
        pressure = {handleJoystickPressure}/>
    <div ref={divRef} />

    <div ref={statusActionDialogRef} className="status_action_dialog" style={{position: 'absolute', visibility: 'hidden', background: 'blue', top: 0, right:0}}>
        <div className="status_action_dialog_content">Do you want to play chess?</div>
        <button className="status_action_dialog_button_yes">Yes</button>
        <button className="status_action_dialog_button_no">No</button>
        <button className="status_action_dialog_button_cancel">Cancel</button>
    </div>

  </>);
  
}