import * as THREE from 'three';

const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;
export class GameCircle{
    
    private scene:THREE.Scene;

    private particles:THREE.Points[]=[];
    private material:THREE.PointsMaterial[]=[];

    constructor(scene:THREE.Scene){
        this.scene = scene;
        this.initPoints();
    }

    Update(){
        for(let i=0; i<this.particles.length;i+=1){
            let dir = 0;
            const time = Date.now() * 0.00005;
            let h = ( 360 * ( 1.0 + time ) % 360 ) / 360;
            if(i%2==0){
              dir=1;
              h=h/8;
            }else{
              dir=-1;
            }
            this.particles[i].rotation.y += Math.random()*0.005*dir;
            this.material[i].color.setHSL( h, 1, 0.5 );
        }
    }
    
    createCircle(material:THREE.PointsMaterial){
        const geometry = new THREE.BufferGeometry();
        var positions = [];
        
        for ( let i = 0; i < 2000; i ++ ) {
          const phi = Math.random( ) * Math.PI * 2;	
          const rnd = Math.random( )
          const r = 0.2 + ( rnd < 0.8 ? 
            Math.pow( Math.random(), 1/8 ):
            Math.pow( ( 1 +  1 * Math.random() ) * Math.random(), 1/8 ) );
          const v = new THREE.Vector3();
          v.x = r * Math.cos( phi );
          v.y = r * Math.sin( phi );
          v.z = Math.random( )*0.5;
          positions.push( v.x, v.z, v.y );
          
        }
        
        geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( positions, 3 ) );
        const particles = new THREE.Points(geometry, material);
        particles.scale.set(40,20,40);
        particles.position.set(120,5,0);
        return particles;
    }
    
    initPoints() {
        this.particles = [];
        const sprite = new THREE.TextureLoader().load( `${PUBLIC_URL}/images/disc.png` );
        for(let i=0;i<2;i+=1){
            const material = new THREE.PointsMaterial( { 
                size: 0.7, 
                sizeAttenuation: true, 
                map: sprite, 
                alphaTest: 0.5, 
                transparent: true  
            } );
            this.material.push(material);
            const circle = this.createCircle(material);
            this.particles.push(circle);
            this.scene.add( circle );
        };
    }

}
