import * as THREE from 'three';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';

const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;

const SCALE = {'chess':0.05,'ludo':5, 'car':1}

export class Status {
	private scene:THREE.Scene;
	public _target:THREE.Object3D;
	private _initialPosition=new THREE.Vector3();
	private loadingDone = false
	private _onLoadModel:CallableFunction;
	private _publicAddress:string;
	private _statusId: string
	private axis = new THREE.Vector3(0.5,1,0).normalize()
	private rad = 0.1

  
	constructor(scene:THREE.Scene, position:THREE.Vector3, publicAddress:string,statusId:string, onLoad:CallableFunction) {
	  this.scene=scene;
	  this._onLoadModel = onLoad;
	  this._initialPosition.copy(position);
	  this._publicAddress = publicAddress
	  this._statusId = statusId
	  this._Init();
	}
  
	get publicAddress(){
	  return this._publicAddress;
	}
	
	_Init() {
	  this._LoadModels();
	}
  
	_LoadModels() {
	  const scope = this;
	  const loader = new FBXLoader();
	  const url = `${PUBLIC_URL}/models/status/chess.fbx`;
	  loader.load(url, (fbxObj) => {
		console.log("FBX", fbxObj);
		
		const obj = fbxObj.children[0]
		const mScale = SCALE['chess']
		obj.scale.set(mScale,mScale,mScale);

		console.log("OBJ", obj);

		scope._target = obj;
		scope._target.position.copy(scope._initialPosition);
		scope.scene.add(scope._target);
		
		scope.loadingDone = true     
		console.log("In async load done func")                                                                                                                                    
		scope._onLoadModel();
	  });

	  if(scope._target){
		scope._target.position.copy(scope._initialPosition);
	  }
	}
  

  
	get Position() {
	  if(this._target===undefined){
		return this._initialPosition;
	  }
	  else{
		return this._target.position;
	  }
	}
  
  
	get Object(){
	  return this._target;
	}

	
  
    
	Update(timeInSeconds) {
		if(this._target){
			this._target.rotateOnAxis(this.axis,this.rad);
		}
	}	
  
	Kill(){
	  this.scene.remove(this._target);
	}
  
  };