
import React, { useRef, useEffect } from 'react'
import {KhoLand} from '../Kholand/KhoLand';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import ReactNipple from "react-nipple";
import nipplejs from 'nipplejs';

const style = {
  position: 'absolute',
  //backgroundColor: 'coral',
  width: '100%',
  height: '100%'
};

export const GameControl = props => {
  
  const divRef = useRef(null)
  const joyStickRef = useRef(null)

  const options={
    mode: "static",
    color: "green",
    position: { top: "50%", left: "50%" }
  }
  let joystick;

  const handleJoystickStart = (evt, data) => {
    console.log("handleJoystickStart")
    props.start(evt, data);
  }

  const handleJoystickEnd = (evt, data) => {
    console.log("handleJoystickEnd")
    props.end(evt, data);
  }

  const handleJoystickMove = (evt, data) => {
    console.log("handleJoystickMove")
    props.move(evt, data);
  }

  const handleJoystickDir = (evt, data) => {
    console.log("handleJoystickDir")
    props.dir(evt, data);
  }

  const handleJoystickPressure = (evt, data) => {
    console.log("handleJoystickPressure")
    props.pressure(evt, data);
  }

  useEffect(() => {  
    const div = divRef.current

    if(joyStickRef){
      joystick = nipplejs.create({
        zone: joyStickRef.current,
        color: 'blue',
        mode: "static",
        position: { bottom: "10%", left: "75%" }
      })[0];

      joystick.on('start', handleJoystickStart);
      joystick.on('end', handleJoystickEnd);
      joystick.on('move', handleJoystickMove);
      joystick.on('dir', handleJoystickDir);
      joystick.on('pressure', handleJoystickPressure);
    }

    return () => {
      //Destroy joystick
      if(joystick){
        console.log("joystick", joystick);
        joystick.destroy();
      }
    }
  }, [])
  
  const joy = {
    outline: `1px dashed ${options.color}`,
    width: 250,
    height: 250
  }
  return (<div 
    style={style}
    ref={divRef} 
    {...props}>
      <div 
        ref={joyStickRef} 
      />

      {/* <ReactNipple
        className="joystick"
        options={options}
        style={joy}
        // onStart={this.handleJoystickStart}
        // onEnd={this.handleJoystickEnd}
        // onMove={this.handleJoystickMove}
        // onDir={this.handleJoystickDir}
        // onPlain={this.handleJoystickPlain}
        // onShown={this.handleJoystickShown}
        // onHidden={this.handleJoystickHidden}
        // onPressure={this.handleJoystickPressure}
      /> */}
      
      </div>)
}

export default GameControl